import { useLayoutEffect, useRef } from 'react';
import { useTagged } from 'react-tagged-state';
import open from '../actions/open';
import openNear from '../actions/openNear';
import flag from '../actions/flag';
import start from '../actions/start';
import boardState from '../states/boardState';
import gameOverState from '../states/gameOverState';
import widthState from '../states/widthState';
import heightState from '../states/heightState';
import generateBoardState from '../utils/generateBoardState';

const CELL_SIZE = 20;

const HALF_CELL_SIZE = CELL_SIZE / 2;

const BOARD_ID = 'BOARD_ID';

const getEventPoint = (event) => {
    console.log(event);
    const rect = document.getElementById(BOARD_ID).getBoundingClientRect();

    return [Math.floor((event.clientY - rect.top) / CELL_SIZE), Math.floor((event.clientX - rect.left) / CELL_SIZE)];
};

const Board = () => {
    const board = useTagged(boardState);
    const gameOver = useTagged(gameOverState);
    const width = useTagged(widthState);
    const height = useTagged(heightState);
    const boardBoard = board ? board.board : generateBoardState(width, height, 0).board;

    useLayoutEffect(() => {
        const ctx = document.getElementById(BOARD_ID).getContext('2d');
        let row = 0;
        let col = 0;
        const rows = boardBoard.length;
        const cols = boardBoard[0].length;
        ctx.fillStyle = 'black';
        ctx.fillRect(0, 0, cols * CELL_SIZE, rows * CELL_SIZE);

        for (; row < rows; ++row) {
            for (col = 0; col < cols; ++col) {
                const cell = boardBoard[row][col];
                const x = cell.col * CELL_SIZE;
                const y = cell.row * CELL_SIZE;

                ctx.fillStyle = cell.isOpened ? 'aliceblue' : 'lightgray';
                ctx.fillRect(x, y, CELL_SIZE - 1, CELL_SIZE - 1);
                ctx.font =
                    "12px -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif";
                ctx.textAlign = 'center';
                ctx.textBaseline = 'middle';

                if (!cell.isOpened) {
                    if (cell.isFlag) {
                        ctx.fillText('ðŸš©', x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);

                        continue;
                    }

                    continue;
                }

                if (cell.isBomb) {
                    if (cell.isFlag) {
                        ctx.fillText('âœ…', x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);

                        continue;
                    }

                    ctx.fillText('ðŸ’£', x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);

                    continue;
                }

                if (!cell.bombCount) {
                    continue;
                }

                switch (cell.bombCount) {
                    case 1: {
                        ctx.fillStyle = 'blue';

                        break;
                    }
                    case 2: {
                        ctx.fillStyle = 'green';

                        break;
                    }
                    case 3: {
                        ctx.fillStyle = 'red';

                        break;
                    }
                    case 4: {
                        ctx.fillStyle = 'purple';

                        break;
                    }
                    case 5: {
                        ctx.fillStyle = 'maroon';

                        break;
                    }
                    case 6: {
                        ctx.fillStyle = 'turquoise';

                        break;
                    }
                    case 7: {
                        ctx.fillStyle = 'black';

                        break;
                    }
                    case 8: {
                        ctx.fillStyle = 'gray';

                        break;
                    }
                }

                ctx.fillText(cell.bombCount, x + HALF_CELL_SIZE - 1, y + HALF_CELL_SIZE);
            }
        }
    }, [boardBoard]);
    const boardWidth = boardBoard[0].length * CELL_SIZE;
    const boardHeight = boardBoard.length * CELL_SIZE;
    const touchTimeoutRef = useRef(null);

    return (
        <canvas
            id={BOARD_ID}
            style={{
                width: boardWidth,
                height: boardHeight,
                userSelect: 'none',
                cursor: 'pointer',
                pointerEvents: gameOver ? 'none' : undefined
            }}
            data-is-over={!!gameOver}
            width={boardWidth}
            height={boardHeight}
            onClick={(event) => {
                const [row, col] = getEventPoint(event);

                if (board) {
                    open(boardBoard[row][col]);

                    return;
                }

                start(row, col);
            }}
            onDoubleClick={
                board
                    ? (event) => {
                          const [row, col] = getEventPoint(event);

                          openNear(boardBoard[row][col]);
                      }
                    : null
            }
            onTouchStart={
                board
                    ? (event) => {
                          const [row, col] = getEventPoint(event.targetTouches[0]);
                          const cell = boardBoard[row][col];

                          touchTimeoutRef.current = setTimeout(() => {
                              if (cell.isOpened) {
                                  openNear(cell);

                                  return;
                              }

                              flag(cell);
                          }, 250);
                      }
                    : null
            }
            onTouchEnd={
                board
                    ? () => {
                          clearTimeout(touchTimeoutRef.current);
                          touchTimeoutRef.current = null;
                      }
                    : null
            }
            onContextMenu={
                board
                    ? (event) => {
                          event.preventDefault();

                          if (!touchTimeoutRef.current) {
                              const [row, col] = getEventPoint(event);

                              flag(boardBoard[row][col]);
                          }
                      }
                    : null
            }
        />
    );
};

export default Board;
